body {
  font-family: sans-serif;
}

h1 {
  color: cadetblue;
  font-style: italic;
}

button {
  background-color: white;
  display: inline-block;
  border-radius: 5px;
  border-color: black;
  color: black;
  text-align: center;
  font-weight: bold;
}

.save {
  font-size: 15px;
  padding: 2px;
}

button:hover {
  background: cadetblue;
}

.note {
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 15px;
}

.newNote {
  border-color: black;
  border-radius: 5px;
  padding: 2px;
  font-size: 15px;
}

ul {
  border-style: solid;
  border-radius: 5px;
  padding-right: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  word-wrap: break-word;
  width: 600px;
}
